<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                v-if="!id"
                label='模板下载'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
        >
            <a :href="templateUrl" download>点击下载</a>
        </a-form-item>
        <a-form-item
                label='上传用户'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="user_id"
        >
            <SelectUser/>
        </a-form-item>
        <a-form-item
                label='分类'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="category_id"
        >
            <SelectExamCategory/>
        </a-form-item>
        <a-form-item
                label='附件'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="more_file"
        >
            <upload-files :length="1"/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getExamExcel, createExamExcel, updateExamExcel} from '@/api/examExcel'
    import SelectUser from '@/components/select/SelectUser'
    import SelectExamCategory from '@/components/select/SelectExamCategory'
    import uploadFiles from '@/components/upload/UploadFiles'

    export default {
        components: {
            uploadFiles, SelectUser, SelectExamCategory
        },
        props: {
            id: {
                required: false,
                type: Number,
                default() {
                    return 0;
                },
            }
        },
        data() {
            return {
                form: {},
            }
        },
        computed: {
            templateUrl(){
                return this.$store.state.common.fileBaseUrl+'考题模板.xlsx';
            }
        },
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getExamExcel(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            user_id: res.data.user_id,
                            category_id: res.data.category_id,
                            more_file: res.data.more.file,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault(e);

                let request;

                let data = this.form.getFieldsValue();
                //

                if (this.id) {
                    request = updateExamExcel(this.id, data)
                } else {
                    request = createExamExcel(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
