import axios from '@/libs/api.request'
import store from '@/store'
import {getCategoryList} from "./category";

const baseUrl = 'exam_categories/';

export const getExamCategoryList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getExamCategorySelectTreeList = () => {
    return getExamCategoryList({current_page: 0, filter: {}}).then(res => {
        let result = [];
        for (let i = 0; i < res.data.rows.length; i++) {
            result.push({
                id: res.data.rows[i].id,
                label: res.data.rows[i].name + ' - 题目 ' + res.data.rows[i].questions_count + ' 题',
                value: res.data.rows[i].id.toString(),
            });

        }
        return result;
    })
}

export const getExamCategory = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const createExamCategory = (d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl,
        method: 'post',
        data,
        params,
    })
}

export const updateExamCategory = (id, d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'put',
        data,
        params,
    })
}

export const deleteExamCategory = (id) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'delete',
        params,
    })
}
